@import url(https://fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  padding: 0;
  color: #222;
  width: 100%;
  background: #EEEEEE;
  font-family: "Open Sans", sans-serif;
}
main {
  font-size: 1.2rem;
  width: 95%;
  margin: 0 auto;
  padding: 1rem 0;
}

h3 {
  font-size: 1.2rem;
  margin: 0 0 4px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.card {
  padding: 0.6rem 1rem;
  margin: 1rem auto;
  background: #FAFAFA;
  border-radius: 2px;
  box-shadow: 1px 1px 4px #BBB;
}

.entry {
  display: block;
  font-family: monospace;
  line-height: 1.2rem;
}

.table {
  display: table;
}

.table>span {
  display: table-cell;
  vertical-align: middle;
  padding-right: 0.2rem;
}

.left {
  float: left;
}

.right {
  float: right;
}

.block {
  display: inline-block;
  width: 7rem;
}

.big {
  font-size: 1.5rem;
}

.done {
  opacity: 0.4;
  -webkit-filter: blur(1px);
          filter: blur(1px);
}
